.container {
    display: flex;
    justify-content: center;
    width: 100%;
    border-bottom: 1px solid #30609e;
}

.form {
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    justify-content: flex-start;
    margin: 20px auto;
}

.label {
    display: block;
    text-align: left;
    font-size: 16px;
    font-weight: 400;
    margin: 5px 0px;
}
.input {
    display: block;
    font-weight: 300;
    font-size: 18px;
    line-height: 1.5;
    letter-spacing: 0.02em;
    outline: none;
    width: 100%;
    padding: 0 12px;
    height: 42px;
    border: 1px solid rgba(105, 137, 196, 0.89);
    border-radius: 5px;
  }

.addContactBtn {
    color: rgb(0, 0, 0);
      font-weight: 400;
      font-size: 18px;
      line-height: 1.2;
      text-align: center;
      letter-spacing: 0.05em;
      padding: 8px;
      border-radius: 25px;
      height: 42px;
      width: 150px;
      border: none;
      background: rgb(197, 219, 241);
      margin-left: 75px;
    }
    
    .addContactBtn:hover {
      background: #30609e;
      color: white;
    }